    if (x === undefined) {
      return styles.fail;
    }

    if (x.potential_option_grade == null) {
      return styles.f;
    }
    if (potentialOptionGrade(x) == null) {
      return styles.f;
    }
    return potentialOptionGrade(x).startsWith("레전드리")
